body{
  --tw-bg-opacity: 1;
  background-color: rgb(0 31 45 / var(--tw-bg-opacity, 1));
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
    background-image: linear-gradient(rgba(0, 27, 68, 0.95), rgba(0, 27, 68, 0.95)),
      repeating-linear-gradient(0deg, transparent, transparent 2px, rgba(255, 255, 255, 0.1) 2px, rgba(255, 255, 255, 0.1) 4px);
    min-height: 100vh;
    image-rendering: pixelated;
}
.font-press-start {
    font-family: 'Press Start 2P', monospace;
  }
.pixel-text {
    text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.5);
    image-rendering: pixelated;
  }
.pixel-art {
    image-rendering: pixelated;
    transform: scale(1.2);
    transition: transform 0.2s ease;
  }
.pixel-art:hover {
    transform: scale(1.4);
  }
.pixel-border {
    position: relative;
    border: 2px solid #FFD700;
    box-shadow: 0 0 0 2px #FFD700;
    image-rendering: pixelated;
  }
.pixel-border::after {
    content: '';
    position: absolute;
    inset: -2px;
    background: linear-gradient(45deg, #FFD700 25%, transparent 25%, transparent 75%, #FFD700 75%);
    background-size: 4px 4px;
    z-index: -1;
    pointer-events: none;
  }
.pixel-button {
    position: relative;
    image-rendering: pixelated;
    transition: all 0.2s ease;
    box-shadow: 4px 4px 0px rgba(0, 0, 0, 0.2);
  }
.pixel-button:hover {
    transform: translate(-2px, -2px);
    box-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);
  }
.pixel-button:active {
    transform: translate(2px, 2px);
    box-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
  }
.nav-bar{
  border-bottom-width: 1px;
  border-color: rgb(255 215 0 / 0.2);
  --tw-bg-opacity: 1;
  background-color: rgb(0 31 45 / var(--tw-bg-opacity, 1));
}
.panel{
  border-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgb(255 215 0 / var(--tw-border-opacity, 1));
  --tw-bg-opacity: 1;
  background-color: rgb(10 36 114 / var(--tw-bg-opacity, 1));
}
.game-panel{
  border-radius: 0.125rem;
  border-width: 3px;
  --tw-border-opacity: 1;
  border-color: rgb(255 215 0 / var(--tw-border-opacity, 1));
  --tw-bg-opacity: 1;
  background-color: rgb(0 31 45 / var(--tw-bg-opacity, 1));
  padding: 1rem;
    box-shadow: 0 0 0 3px #FFD700;
    position: relative;
}
.game-panel::before {
    content: '';
    position: absolute;
    inset: -3px;
    background: linear-gradient(45deg, #FFD700 25%, transparent 25%, transparent 75%, #FFD700 75%);
    background-size: 6px 6px;
    z-index: -1;
  }
.game-text {
    font-family: 'Press Start 2P', monospace;
    font-family: Press Start 2P, monospace;
    font-size: 0.875rem;
    line-height: 1.25rem;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
  }
.game-button {
    font-family: 'Press Start 2P', monospace;
    position: relative;
    border-width: 2px;
    --tw-border-opacity: 1;
    border-color: rgb(255 215 0 / var(--tw-border-opacity, 1));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    font-family: Press Start 2P, monospace;
    font-size: 0.875rem;
    line-height: 1.25rem;
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
  }
.game-button:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(255 215 0 / var(--tw-bg-opacity, 1));
  --tw-text-opacity: 1;
  color: rgb(0 31 45 / var(--tw-text-opacity, 1));
}
.game-button {
    box-shadow: 4px 4px 0px rgba(0, 0, 0, 0.2);
  }
.game-button:hover {
    transform: translate(-2px, -2px);
    box-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);
  }
.game-button:active {
    transform: translate(2px, 2px);
    box-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
  }
.game-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
.game-value{
  --tw-text-opacity: 1;
  color: rgb(255 215 0 / var(--tw-text-opacity, 1));
}
.claim-button {
    font-family: 'Press Start 2P', monospace;
    --tw-bg-opacity: 1;
    background-color: rgb(255 215 0 / var(--tw-bg-opacity, 1));
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    font-family: Press Start 2P, monospace;
    --tw-text-opacity: 1;
    color: rgb(0 31 45 / var(--tw-text-opacity, 1));
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
  }
.claim-button:hover{
  --tw-bg-opacity: 0.9;
}
.claim-button:disabled{
  opacity: 0.5;
}
.claim-button {
    box-shadow: 4px 4px 0px rgba(0, 0, 0, 0.2);
  }
.claim-button:hover {
    transform: translateY(-2px);
    box-shadow: 6px 6px 0px rgba(0, 0, 0, 0.2);
  }
.claim-button:active {
    transform: translateY(0);
